/**
 * Ê∑±Êã∑Ë¥ù
 * // === ÊÄùË∑ØÔºöÊ†πÊçÆtargetÁ±ªÂûãÁîüÊàêÊñ∞ÂÆû‰æãÔºåÂ¶ÇÁ©∫ÂØπË±°ÊàñÁ©∫Êï∞ÁªÑÔºõÁÑ∂ÂêéÈÄöËøá for..in ÈÅçÂéÜÂèØÊûö‰∏æÂ±ûÊÄßÔºåÂπ∂ÈÄöËøáhasOwnPropertyÊù•ËøáÊª§ÁõÆÊ†áÂØπË±°‰∏äÊâçÊúâÁöÑÂ±ûÊÄßÔºõÂú®ËµãÂÄºÁöÑÊó∂ÂÄôÔºåÊ†πÊçÆÁ±ªÂûãÊù•ÂÜ≥ÂÆöÊòØÂê¶ÈúÄË¶ÅÈÄíÂΩí === //
 * @param {Object|Array} target - Êã∑Ë¥ùÁõÆÊ†á
 * @return {Object|Array}
 */

export default function deepCopy (target) {
  if (typeof target !== 'object') {
    return
  }

  let newObj = target instanceof Array ? [] : {}

  for (const prop in target) {
    if (target.hasOwnProperty(prop)) {
      newObj[prop] = typeof target[prop] === 'object' ? deepCopy(target[prop]) : target[prop]
    }
  }

  return newObj
}
