import deepCopy from '../../../lib/deepCopy'
import { findProperty, swap } from './sort'

/**
 * Â∏åÂ∞î
 * // === ÊÄùÊÉ≥Ôºö[a, b, c, d, e], ÂÖàËÆ°ÁÆóÂá∫Âä®ÊÄÅÈó¥Èöî, ÊØîÂ¶Çh = h * 3 + 1ÔºåÁÑ∂ÂêéÂ§ñÂæ™ÁéØ‰ªéhÂºÄÂßãÈÄê‰∏ÄÈÅçÂéÜÔºåÂÜÖÂæ™ÁéØÂ∞ÜÂ§ñÂæ™ÁéØÈÄâÂÆöÁöÑÂÖÉÁ¥†ÂèäÂÖ∂Ââçh‰ΩçÁΩÆÁöÑÂÖÉÁ¥†ÊØîËæÉÔºåÂΩìÂ§ñÂæ™ÁéØÈÅçÂéÜÂà∞ÊúÄÂêéÊó∂ÔºåÂÜçÊ¨°Âä®ÊÄÅËÆ°ÁÆóh‰∏∫(h - 1) / 3, Âç≥‰ª•Êõ¥Â∞èÁöÑÈó¥ÈöîÈáçÂ§çÂàöÊâçÁöÑÊìç‰ΩúÔºåÁõ¥Âà∞h‰∏∫1 === //
 * // === ‰ºòÂäøÔºöËæÉÂø´Âú∞Â∞ÜËæÉËøúÁöÑÂÖÉÁ¥†ÂΩíÂà∞Ê≠£Á°ÆÁöÑ‰ΩçÁΩÆ === //
 * @param {Array} arr - Êï∞ÁªÑ
 * @param {Boolean} sequence - true‰∏∫Ê≠£Â∫èÔºåfalse‰∏∫ÂÄíÂ∫è
 * @param {Array} property - ÊéíÂ∫èÊó∂ÊØîËæÉÁöÑÂ±ûÊÄßÂêç
 * @return {Array}
 */

export function shellSort (arr, property, sequence) {

  arr = deepCopy(arr)

  /**
   * Âà§Êñ≠ÂáΩÊï∞
   */
  const judge = function (inner, h, sequence, property) {
    if (sequence === true) {
      return findProperty(arr[inner], property) < findProperty(arr[inner - h], property)
    } else {
      return findProperty(arr[inner], property) > findProperty(arr[inner - h], property)
    }
  }

  const len = arr.length
  let h = 1

  // ÂçáÁ∫ßÁâàÁöÑÊèíÂÖ•ÊéíÂ∫è, Âè™ÊòØÂ∞ÜÊèíÂÖ•ÊéíÂ∫è‰∏≠ÊØèÊ¨°‰∏éÂâç‰∏Ä‰∏™ÊØîËæÉ ÊîπÂèòÊàê ‰∏éÂâçh‰∏™ÊØîËæÉ
  // hÊòØÂ∏åÂ∞îÊéíÂ∫èÁöÑÂÖ≥ÈîÆ
  // ËÉΩÊõ¥Âø´Âú∞Â∞Ü‰∏çÊ≠£Á°ÆÁöÑÂÖÉÁ¥†‰ªéËæÉËøúÁöÑÂú∞ÊñπÊãâÂõûÊù•
  while (h < len / 3) {
    h = h * 3 + 1
  }

  while (h >= 1) {

    let outer
    let inner

    for (outer = h; outer < len; ++outer) {
      for (inner = outer; inner >= h && judge(inner, h, sequence, property); inner -= h) {
        swap(arr, inner, inner - h)
      }
    }

    h = (h - 1) / 3
  }

  return arr
}