import deepCopy from '../../../lib/deepCopy'
import { findProperty, swap } from './sort'

/**
 * Âø´Êéí
 * // === 1 ÊÄùÊÉ≥Ôºö[a, b, c, d, e], Âà©Áî®ÂàÜÊ≤ªÂèëÔºåÂÆöc‰∏∫‰∏≠Èó¥ÂÄºÔºåÁÑ∂ÂêéÂàÜÂà´‰ªéaÂíåeÂêëcÈÅçÂéÜÔºåÂàÜÂà´ÊâæÂà∞Â∑¶ËæπÊØîcÂ§ß, Âè≥ËæπÊØîcÂ∞èÁöÑÂÄºÔºåÊØîÂ¶Ça,e; ÁÑ∂Âêé‰∫§Êç¢‰ΩçÁΩÆ, ‰æùÊ¨°Á±ªÊé®ÔºõÊúÄÂêé‰∏ÄÂÆöÊòØcÂ∑¶ËæπÁöÑÂÄºÊØîcÂ∞èÔºåÂè≥ËæπÁöÑÂÄºÈÉΩÊØîcÂ§ßÔºõÂÜçÂàÜÂà´ÂØπcÂ∑¶ËæπÁöÑÂÄºÂíåÂè≥ËæπÁöÑÂÄºÁî®ÂêåÊ†∑ÁöÑÂèñ‰∏≠Èó¥ÂÄºÊØîËæÉÁöÑÊñπÂºèÈÄêÂ±ÇÁöÑÈÄíÂΩíÁõ¥Âà∞ÂàíÂàÜÂá∫Êù•ÈúÄË¶ÅÊØîËæÉÁöÑÁ≥ªÂàóÁöÑÈïøÂ∫¶Âè™Êúâ1‰∏∫Ê≠¢ === //
 * // === 2 ÊúÄÂ•ΩÊÉÖÂÜµÂ§çÊùÇÂ∫¶ÂàÜÊûê: === //
 *
 * ÈÄíÂΩíÁÆóÊ≥ïÁöÑÊó∂Èó¥Â§çÊùÇÂ∫¶ÂÖ¨ÂºèÔºöT[n] = aT[n/b] + f(n)
 *
 * ÊúÄ‰ºòÁöÑÊÉÖÂÜµÂ∞±ÊòØÊØè‰∏ÄÊ¨°ÂèñÂà∞ÁöÑÂÖÉÁ¥†ÈÉΩÂàöÂ•ΩÂπ≥ÂàÜÊï¥‰∏™Êï∞ÁªÑ: T[n] = 2T[n/2] + f(n)
 * T[n/2]‰∏∫Âπ≥ÂàÜÂêéÁöÑÂ≠êÊï∞ÁªÑÁöÑÊó∂Èó¥Â§çÊùÇÂ∫¶
 * f[n] ‰∏∫Âπ≥ÂàÜËøô‰∏™Êï∞ÁªÑÊó∂ÊâÄËä±ÁöÑÊó∂Èó¥
 *
 * T[n] =  2T[n/2] + n         // Á¨¨‰∏ÄÊ¨°ÈÄíÂΩí
 * ‰ª§Ôºön = n/2       T[n] =  2 { 2 T[n/4] + (n/2) }  + n    // Á¨¨‰∫åÊ¨°ÈÄíÂΩí
 *                        =  2^2 T[ n/ (2^2) ] + 2n
 * ‰ª§Ôºön = n/(2^2)  T[n]  =  2^2  {  2 T[n/ (2^3) ]  + n/(2^2)}  +  2n
 *                        =  2^3 T[  n/ (2^3) ]  + 3n   // Á¨¨‰∏âÊ¨°ÈÄíÂΩí
 * ‰ª§Ôºön = n/(  2^(m-1) )    =  2^m T[1]  + mn    // Á¨¨mÊ¨°ÈÄíÂΩí
 * 
 * ÂΩìÊúÄÂêéÂπ≥ÂàÜÁöÑ‰∏çËÉΩÂÜçÂπ≥ÂàÜÊó∂Ôºå‰πüÂ∞±ÊòØËØ¥ÊääÂÖ¨Âºè‰∏ÄÁõ¥ÂæÄ‰∏ãË∑åÂÄíÔºåÂà∞ÊúÄÂêéÂæóÂà∞T[1]Êó∂ÔºåËØ¥ÊòéËøô‰∏™ÂÖ¨ÂºèÂ∑≤ÁªèËø≠‰ª£ÂÆå‰∫ÜÔºàT[1]ÊòØÂ∏∏Èáè‰∫ÜÔºâ
 *
 * @param {Array} arr - Êï∞ÁªÑ
 * @param {Boolean} sequence - true‰∏∫Ê≠£Â∫èÔºåfalse‰∏∫ÂÄíÂ∫è
 * @param {Array} property - ÊéíÂ∫èÊó∂ÊØîËæÉÁöÑÂ±ûÊÄßÂêç
 * @return {Array}
 */

export function quickSort (arr, property, sequence) {

  // Ê∑±Êã∑Ë¥ù‰∏Ä‰ªΩÊï∞ÁªÑ, ‰πãÂêéÁöÑÊâÄÊúâÊìç‰ΩúÈÉΩÂØπËøô‰ªΩÊï∞ÁªÑÊìç‰Ωú
  arr = deepCopy(arr)

  /**
   * Âà§Êñ≠ÂáΩÊï∞
   */
  const judge = (sequence, property, index, middle) => {
    if (sequence === true) {
      return new Date(findProperty(arr[index], property)).getTime() < new Date(findProperty(arr[middle], property)).getTime()
    } else {
      return new Date(findProperty(arr[index], property)).getTime() > new Date(findProperty(arr[middle], property)).getTime()
    }
  }

  /**
   * Ê†πÊçÆleftÂíårightÂ∞Üarr‰∏≠Ê≠§ËåÉÂõ¥ÁöÑÊï∞ÊçÆÂàÜ‰∏∫‰∫å, ËÆ°ÁÆóÂá∫‰∏≠Èó¥ÁöÑÂàÜÂâ≤ÂÄº, Â∑¶ËæπÁöÑÊØîÂÆÉÈÉΩÂ∞è, Âè≥ËæπÁöÑÊØîÂÆÉÈÉΩÊâì
   * @param {Number} left - Â∑¶ËæπÁïåÁ¥¢Âºï
   * @param {Number} right - Âè≥ËæπÁïåÁ¥¢Âºï
   * @return {Number} ‰∏≠Èó¥ÂÄºÁ¥¢Âºï
   * @private
   */
  const partition = (left, right) => {

    // ËÆ°ÁÆóÂá∫ÁªôÂÆöËåÉÂõ¥ÂÜÖÁöÑ‰∏≠Èó¥Á¥¢ÂºïÂÄº‰Ωú‰∏∫ÂèÇËÄÉÊ†áÂáÜ
    const middle = (left + right) >> 1
    // Â§çÂà∂Â∑¶Âè≥ËæπÁïåÁ¥¢Âºï
    let i = left
    let j = right

    // ÂΩìÂ∑¶ËæπÁïåÁ¥¢ÂºïÊØîÂè≥ËæπÁïåÁ¥¢ÂºïÂ∞è
    // ËØÅÊòéËøòÂèØ‰ª•ÊØîËæÉ
    while (i <= j) {

      // Â¶ÇÊûúÁ¥¢Âºïi‰ΩçÁΩÆÁöÑÂÄºÊØîmiddle‰ΩçÁΩÆÁöÑÂÄºÂ∞è
      // ‰∏ãÊ†áiÁªßÁª≠ÂêëmiddleÊñπÂêëÁßªÂä®
      while (judge(sequence, property, i, middle)) {
        ++i
      }

      // Â¶ÇÊûúÁ¥¢Âºïj‰ΩçÁΩÆÁöÑÂÄºÊØîmiddle‰ΩçÁΩÆÁöÑÂÄºÂ§ß
      // ‰∏ãÊ†ájÁªßÁª≠ÂêëmiddleÊñπÂêëÁßªÂä®
      while (judge(!sequence, property, j, middle)) {
        --j
      }

      // ÂΩì‰∏äÈù¢‰∏§‰∏™Âæ™ÁéØÊúâÂèëÁé∞‰∏çÁ¨¶ÁöÑ
      // Âú®Ê≠§Êó∂ ‰∏ãÊ†áiÊâÄÊåáÁöÑ‰ΩçÁΩÆ Âíå ‰∏ãÊ†ájÊâÄÊåáÁöÑ‰ΩçÁΩÆ ËøòÊ≤° Áõ∏Ë∂ä
      // ËØÅÊòé ‰∏ãÊ†áiÊâÄÊåáÁöÑÂÄº ÊØîmiddleÂ§ß, ‰∏ãÊ†ájÊâÄÊåáÁöÑ‰ΩçÁΩÆÊØîmiddleÂ∞è
      // ‰∫íÊç¢‰ΩçÁΩÆ
      // ‰∏ãÊ†á i j ÁªßÁª≠ÂêëmiddleÁßªÂä®
      // Áõ¥Âà∞ ‰∏ãÊ†ái/j Áõ∏Ë∂äÂàô‰ΩøÂæó middleÂ∑¶ËæπËåÉÂõ¥ÁöÑÂÄº ÊØî middleÊâÄÊåáÁöÑÂÄºÂ∞è, middleÂè≥ËæπËåÉÂõ¥ÁöÑÂÄº ÈÉΩÊØî middleÊâÄÊåáÁöÑÂÄº Â§ß: [left, i) [j, right)
      if (i <= j) {
        swap(arr, i, j)
        ++i
        --j
      }
    }

    // ÊúÄÂêé‰∏ãÊ†áiÁöÑÂÄº‰∏∫ ‰∏ãÊ†áj
    return i
  }

  /**
   * Ê†πÊçÆÁªôÂá∫ÁöÑleftÂíårightËæπÁïå, ËÆ°ÁÆóÂá∫ middle, ÂÜçËÆ© Â∑¶ËåÉÂõ¥ Âíå Âè≥ËåÉÂõ¥ ÁöÑÊï∞ÊçÆÂÅöÂêåÊ†∑ÁöÑ‰∫åÂàÜ
   */
  const quick = (left, right) => {
    let middle

    // ÂΩìÊï∞ÁªÑÈïøÂ∫¶Â§ß‰∫é1ÁöÑÊó∂ÂÄô
    if (arr.length > 1) {

      // ËÆ°ÁÆóÂá∫ËØ•Êï∞ÁªÑ‰∏≠Èó¥ÂÄºÁöÑÁ¥¢Âºï
      // ËØ•ËÆ°ÁÆó‰ΩøÂæóËØ•Á¥¢ÂºïÂÄºÂ∑¶ËæπÁöÑÊâÄÊúâÂÖÉÁ¥†ÊØîÂÆÉÂ∞è, Âè≥ËæπÁöÑÊâÄÊúâÂÖÉÁ¥†ÊØîÂÆÉÂ§ß
      middle = partition(left, right)

      // ‰ª•Áõ∏ÂêåÁöÑÊñπÂºèÂ§ÑÁêÜ [left, middle) Âå∫Èó¥ÁöÑÂÖÉÁ¥†
      if (left < middle - 1) {
        quick(left, middle - 1)
      }

      // ‰ª•Áõ∏ÂêåÊñπÂºèÂ§ÑÁêÜ [middle, right] Âå∫Èó¥ÁöÑÂÖÉÁ¥†
      if (middle < right) {
        quick(middle, right)
      }

    }

    return arr
  }

  return quick(0, arr.length - 1)
}