    // === httpsÁ´ôÁÇπ‰∏≠ÂºïÂÖ•‰∫ÜhttpËµÑÊ∫ê: ÊµèËßàÂô®ÈªòËÆ§httpsÁ´ôÁÇπ‰∏≠‰∏çËÉΩÂºïÂÖ•httpËµÑÊ∫ê, https‰ºöÂ§±ÊïàÔºåÂá∫Áé∞ ËøûÊé•‰∏çÂÆâÂÖ® logo === //
    // === 1 Ëß£ÂÜ≥ÂäûÊ≥ï: === //
    // === 1.1 script ‰∏çËÆ∫ÊòØÊîπ‰∏∫httpsÂçèËÆÆËøòÊòØÈÄöËøáÁõ∏ÂØπÂçèËÆÆ, ËôΩÁÑ∂ËÉΩËØ∑Ê±ÇÂõûÊù•, ‰ΩÜÊòØËã•ËµÑÊ∫êÊòØÁî®httpÂçèËÆÆÁöÑ, ‰ºö‰ΩøÂæóÊï¥Á´ôÁöÑhttpsÁ†¥ÂùèÊéâ === //
    // === 1.2 ajaxÁöÑËØùÊòØ‰∏çËÉΩÂú®https‰∏≠ËØ∑Ê±ÇhttpÁöÑ, ÈúÄÁªô‰∏∫iframe, ‰∏§ËæπËøõË°åÊéßÂà∂ === //
    // === 2 Áõ∏ÂØπÂçèËÆÆ: Â∞ÜÂçèËÆÆÂéªÊéâ, Áïô‰∏ã // ‰ª•Âèä ÂêéÈù¢ÁöÑÂÜÖÂÆπ, Âú®‰ΩøÁî®httpsÁöÑÁΩëÁ´ô‰∏≠‰ºö‰ΩøÁî®httpsÂçèËÆÆ, Âú®‰ΩøÁî®httpÁöÑÁΩëÁ´ô‰∏≠‰ºöÊòØÂÖàÁî®httpÂçèËÆÆ === //
    
    // jsonp.get('https://api.ipify.org?format=jsonp&callback=jp.getIp', 'ip')
    jsonp.get(`//api.k780.com/?app=ip.local&appkey=${appkey}&sign=${sign}&format=json&jsoncallback=jp_location.getIp`, 'ip')
    jsonp.delete('ip')

