  // === 2 ÁªëÂÆöthis Ë¶ÅÊîæÂà∞constructor ‰∏≠Âéª, Âú®jsx‰∏≠ onClick={this.clickHandle.bind(this)} ‰ºöÂú®ÊØèÊ¨°ÊâßË°årenderÊó∂ÈÉΩ‰ºöÊâßË°åbindÂáΩÊï∞, onClick={() => this.clickHandle()} ‰ºöÂú®ÊØèÊ¨° render Êó∂ÈÉΩÂ£∞ÊòéÊñ∞ÁöÑÂáΩÊï∞ === //
  // === 3 Âú®‰º†ÁªôÂ≠êÁªÑ‰ª∂ÂÄºÊó∂ ÂÅöÂà∞ ÈúÄË¶Å‰ªÄ‰πà‰º†‰ªÄ‰πà, {...this.state} ËøôÊ†∑ÁöÑÂÜôÊ≥ï ‰ºö‰º†ÂæàÂ§ö È¢ùÂ§ñÁöÑÂÄº === //
