// === ÊüØÈáåÂåñ: Â∞Ü‰ΩøÁî®Â§ö‰∏™ÂèÇÊï∞ÁöÑ‰∏Ä‰∏™ÂáΩÊï∞ËΩ¨Êç¢Êàê‰ΩøÁî®‰∏ÄÁ≥ªÂàó‰ΩøÁî®‰∏Ä‰∏™ÂèÇÊï∞ÁöÑÂáΩÊï∞ÁöÑÊäÄÂ∑ßÔºå‰ª•ËææÂà∞ÂèÇÊï∞Â§çÁî®ÁöÑÁõÆÁöÑ === //
// === 1 ÊÄùË∑Ø: Â∞ÜÈúÄË¶ÅÊüØÈáåÂåñÁöÑÂáΩÊï∞fnÁöÑÂèÇÊï∞‰∏™Êï∞‰øùÂ≠òËµ∑Êù•ÔºåÊØèÊ¨°Ë∞ÉÁî®Âè™‰º†‰∏Ä‰∏™ÂèÇÊï∞ÔºåÂè™Ë¶Å‰º†ÂÖ•ÁöÑÂèÇÊï∞ÊÄª‰∏™Êï∞Êú™ËææÂà∞ÊÄª‰∏™Êï∞ÔºåÂ∞±‰ºöÈáçÂ§çÊâßË°åcurryÂáΩÊï∞‰ª•‰øùÂ≠òÂèÇÊï∞ÔºåÂê¶ÂàôÊâßË°åfn === //
export default function _curring (fn, args = []) {
  const len = fn.length

  return function (...param) {

    const _args = args
      .slice(0)
      .concat(param)

    return _args.length < len ? _curring.call(this, fn, _args) : fn.apply(this, _args)

  }
}

// === Â§öÈáçÁÆ≠Â§¥ÂáΩÊï∞ÂÜôÊ≥ï: === //
// === 1 Êö¥Èú≤‰∏Ä‰∏™Âêç‰∏∫curryÁöÑÂáΩÊï∞ÔºåÊé•Âèó‰∏Ä‰∏™ÂèÇÊï∞fnÔºåËøîÂõû‰∏Ä‰∏™Âêç‰∏∫judgeÁöÑÂáΩÊï∞ÔºåjudgeÂáΩÊï∞Êé•ÂèóÂ§ö‰∏™ÂèÇÊï∞ÔºåÈÄöËøárestÂèÇÊï∞Â∞ÜÊØèÊ¨°‰º†ËøõÊù•ÁöÑÂèÇÊï∞ÈÉΩÁªÑÂêàÊàêÊï∞ÁªÑ === //
// === 2 judgeÂáΩÊï∞ËøîÂõû‰∏Ä‰∏™‰∏âÂÖÉËøêÁÆóÁ¨¶: Ëã•Êú¨Ê¨°‰º†Êù•ÁöÑÂèÇÊï∞‰∏™Êï∞‰∏∫fnÁöÑ‰∏™Êï∞, ÂàôÊâßË°åfnÔºõÂê¶ÂàôÔºåÁªßÁª≠ËøîÂõû‰∏Ä‰∏™ÂáΩÊï∞ÔºåËØ•ÂáΩÊï∞Êé•Âèó‰∏Ä‰∏™ÂèÇÊï∞argÔºåËøîÂõûjudgeÂáΩÊï∞ÁöÑË∞ÉÁî®ÔºåÂπ∂Â∞ÜÂà∞ÁõÆÂâç‰∏∫Ê≠¢ÁõÆÂâç‰º†ËøõÊù•ÁöÑÂèÇÊï∞Êï∞ÁªÑ ‰ª•Âèä Êú¨Ê¨°‰º†Êù•ÁöÑÂèÇÊï∞‰º†Áªôjudge === //
// === 3 ÂèÇÊï∞ÁöÑÁßØÁ¥Ø‰∏ªË¶ÅÊòØ‰æùÈù† rest ÂèÇÊï∞ÂÆåÊàêÁöÑ === //
export const curry = fn =>
  judge = (...args) =>
    args.length === fn.length() ?
      fn(...args) :
      arg => judge(...args, arg)
