import deepCopy from '../../../lib/deepCopy'
import { findProperty, swap } from './sort'

/**
 * ÂΩíÂπ∂
 * // === ÊÄùÊÉ≥Ôºö[a, b, c, d ,e], Áî®ÂàÜÊ≤ªÂàíÂá∫Â∑¶Âè≥Êï∞ÁªÑÔºåÈÄíÂΩíÁõ¥Âà∞ÈïøÂ∫¶‰∏∫1ÔºõÊØîÂ¶Ça‰∏ébÊØîËæÉÔºåc‰∏édÊØîËæÉÔºåÊéíÂ∫èÂêéÊîæÂÖ•Êñ∞Êï∞ÁªÑËøîÂõûÔºå‰πãÂêéÔºåcd‰∏éeÊØîËæÉÔºåab‰∏écdeÊØîËæÉ === //
 * @param {Array} arr - Êï∞ÁªÑ
 * @param {Boolean} sequence - true‰∏∫Ê≠£Â∫èÔºåfalse‰∏∫ÂÄíÂ∫è
 * @param {Array} property - ÊéíÂ∫èÊó∂ÊØîËæÉÁöÑÂ±ûÊÄßÂêç
 * @return {Array}
 */

function merge (left, right, property, sequence) {

  /**
   * Âà§Êñ≠ÂáΩÊï∞
   */
  const judge = () => {
    if (sequence === true) {
      return findProperty(left[0], property) < findProperty(right[0], property)
    } else {
      return findProperty(left[0], property) > findProperty(right[0], property)
    }
  }

  // ÁªìÊûúÊï∞ÁªÑ
  let ret = []

  // ‰∏ÄÂºÄÂßãÊØè‰∏§‰∏§ÈïøÂ∫¶‰∏∫1ÁöÑÊï∞ÁªÑ‰∏≠ÁöÑÂÖÉÁ¥†ËøõË°åÈ°∫Â∫èÊØîËæÉ
  // ÈÄöËøáÂà§Êñ≠, Â∞èÁöÑÂÖàÊîæËøõ ret, Â§ßÁöÑÂêéÊîæËøõret
  while (left.length > 0 && right.length > 0) {
    if (judge()) {
      ret.push(left.shift())
    } else {
      ret.push(right.shift())
    }
  }

  // Ëã•ÂÅöÂÆå‰ª•‰∏äÊìç‰ΩúÂêé, leftÊï∞ÁªÑ ÊàñËÄÖ rightÊï∞ÁªÑË¶ÅÊúâÂâ©‰ΩôÁöÑ, ËØÅÊòéÊòØÊØîËæÉÊØîret‰∏≠ÁöÑÈÉΩÂ§ß, ÂàôÁõ¥Êé•‰∏Ä‰∏™‰∏™ÊîæËøõÂéªÂ∞±Â•Ω‰∫Ü
  while (left.length > 0) {
    ret.push(left.shift())
  }
  while (right.length > 0) {
    ret.push(right.shift())
  }

  return ret
}

/**
 * ‰∏çÊñ≠Âú∞‰∫åÂàÜÊï∞ÁªÑ, Áõ¥Âà∞ÂàíÂàÜ‰∏∫ÊØè‰∏™Êï∞ÁªÑÈïøÂ∫¶‰∏∫1‰∏∫Ê≠¢
 */
function _mergeSort (arr, property, sequence) {

  const l = arr.length
  if (l === 1) {
    return arr
  }

  const middle = l >> 1
  let left = arr.slice(0, middle)
  let right = arr.slice(middle, arr.length)

  return merge(_mergeSort(left, property, sequence), _mergeSort(right, property, sequence), property, sequence)
}


export function mergeSort (arr, property, sequence) {
  arr = deepCopy(arr)
  return _mergeSort(arr, property, sequence)
}
